// frontend/src/components/BedCard.jsx

import React, { useState } from 'react';
import axios from 'axios';

const API_BASE_URL = 'http://localhost:3000/api/v1';

const BedCard = ({ bedData, onStatusChange, token }) => {
    // Sync local state with initial bedData
    const [status, setStatus] = useState(bedData.current_status);
    const bedId = bedData.bed_id;
    
    // --- Styles for the Card ---
    const getStatusColor = (currentStatus) => {
        switch (currentStatus) {
            case 'AVAILABLE': return 'bg-green-100 text-green-800 border-green-300';
            case 'OCCUPIED': return 'bg-red-100 text-red-800 border-red-300';
            case 'CLEANING': return 'bg-yellow-100 text-yellow-800 border-yellow-300';
            default: return 'bg-gray-100 text-gray-800 border-gray-300';
        }
    };

    // --- Core Logic: Status Update ---
    const handleStatusUpdate = async (newStatus) => {
        // Descriptive confirmation messages for clinical safety
        const actionText = newStatus === 'CLEANING' ? 'DISCHARGE patient and start cleaning' : `set to ${newStatus}`;
        
        if (!window.confirm(`Confirm: Are you sure you want to ${actionText} for Bed ${bedId}?`)) {
            return;
        }

        try {
            // Using your existing endpoint structure
            await axios.post(`${API_BASE_URL}/beds/${bedId}/status`, 
                { new_status: newStatus },
                {
                    headers: { 'Authorization': `Bearer ${token}` }
                }
            );
            
            setStatus(newStatus); 
            onStatusChange(); // This updates the Hospital Census counts!

        } catch (error) {
            console.error('Error updating status:', error.response ? error.response.data : error.message);
            alert('Failed to update status. Check console for details.');
        }
    };
    
    return (
        <div className={`p-4 border-2 rounded-lg shadow-sm transition-all ${getStatusColor(status)}`}>
            <div className="flex justify-between items-start">
                <h3 className="font-bold text-lg">Bed {bedId}</h3>
                <span className="text-[10px] uppercase font-bold px-2 py-0.5 rounded-full border border-current">
                    {status}
                </span>
            </div>
            
            <div className="mt-2 text-sm">
                <p><span className="font-semibold">Ward:</span> {bedData.ward_name}</p>
                <p><span className="font-semibold">Specialty:</span> {bedData.specialty_type}</p>
            </div>
            
            <div className="mt-4 flex flex-col gap-2">
                {/* DISCHARGE: Only show if currently Occupied */}
                {status === 'OCCUPIED' && (
                    <button 
                        className="bg-red-600 text-white font-bold py-2 rounded-md text-xs hover:bg-red-700 transition-colors"
                        onClick={() => handleStatusUpdate('CLEANING')}>
                        Discharge Patient
                    </button>
                )}

                {/* CLEANING COMPLETE: Only show if currently Cleaning */}
                {status === 'CLEANING' && (
                    <button 
                        className="bg-yellow-600 text-white font-bold py-2 rounded-md text-xs hover:bg-yellow-700 transition-colors"
                        onClick={() => handleStatusUpdate('AVAILABLE')}>
                        Mark Cleaning Complete
                    </button>
                )}

                {/* MANUAL OCCUPY: For admissions not through transfer list */}
                {status === 'AVAILABLE' && (
                    <button 
                        className="bg-green-600 text-white font-bold py-2 rounded-md text-xs hover:bg-green-700 transition-colors"
                        onClick={() => handleStatusUpdate('OCCUPIED')}>
                        Direct Admission
                    </button>
                )}
            </div>
        </div>
    );
};

export default BedCard;