// frontend/src/pages/DashboardPage.jsx

import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import BedCard from '../components/BedCard';
import TransferRequestForm from '../components/TransferRequestForm';
import TransferRequestList from '../components/TransferRequestList';
import HospitalCensus from '../components/HospitalCensus'; // 1. Import the Census

// --- Configuration ---
const API_BASE_URL = 'http://localhost:3000/api/v1';

const SPECIALTY_OPTIONS = [
    'All', 'General', 'ICU', 'Pediatric', 'HDU', 
    'CCU', 'NICU', 'Emergency beds', 'PACU', 'PICU'
];

const DashboardPage = () => {
    const [beds, setBeds] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState(null);
    const [selectedSpecialty, setSelectedSpecialty] = useState('All');
    
    const token = localStorage.getItem('authToken'); 

    // --- 1. Fetch Bed Data ---
    const fetchBedData = useCallback(async (specialty) => {
        setIsLoading(true);
        setError(null);
        
        try {
            let url = `${API_BASE_URL}/beds`;
            if (specialty && specialty !== 'All') {
                url += `?specialty_type=${specialty}`;
            }

            const response = await axios.get(url, {
                headers: { 'Authorization': `Bearer ${token}` }
            });
            
            setBeds(response.data);
        } catch (err) {
            console.error('Failed to fetch bed data:', err);
            if (err.response?.status === 401) {
                setError("Session expired. Please log in again.");
            } else {
                 setError('Could not load bed data. Please check the API server.');
            }
        } finally {
            setIsLoading(false);
        }
    }, [token]);

    useEffect(() => {
        fetchBedData(selectedSpecialty);
    }, [selectedSpecialty, fetchBedData]);

    const handleFilterChange = (event) => {
        setSelectedSpecialty(event.target.value);
    };

    if (error) return (
        <div className="flex items-center justify-center h-screen">
            <div className="bg-red-50 p-6 rounded-lg text-red-700 border border-red-200">
                <p className="font-bold">Error: {error}</p>
                <button onClick={() => window.location.href = '/'} className="mt-2 underline">Go to Login</button>
            </div>
        </div>
    );

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Navigation Header */}
            <header className="bg-indigo-900 text-white p-4 shadow-lg flex justify-between items-center">
                <h1 className="text-2xl font-bold flex items-center">
                    <span className="mr-2">üè•</span> No Bed Syndrome Command Center
                </h1>
                <div className="flex items-center space-x-6">
                    <div className="hidden md:block">
                        <p className="text-xs uppercase tracking-wider text-indigo-300">Logged in as</p>
                        <p className="text-sm font-medium">Hospital Staff</p>
                    </div>
                    <button 
                        onClick={() => { localStorage.clear(); window.location.href = '/'; }}
                        className="bg-red-500 hover:bg-red-600 px-4 py-2 rounded text-sm font-bold transition-colors"
                    >Logout</button>
                </div>
            </header>

            {/* Main Application Layout */}
            <main className="p-6 max-w-[1600px] mx-auto">
                
                {/* 2. Hospital Census Overview Section */}
                <HospitalCensus beds={beds} />

                <div className="grid grid-cols-1 xl:grid-cols-12 gap-6">
                    
                    {/* SECTION 1: Bed Inventory Management (Left Side - 8/12 Columns) */}
                    <div className="xl:col-span-8 space-y-6">
                        <section className="bg-white p-6 rounded-xl shadow-sm border border-gray-100">
                            <div className="flex flex-col md:flex-row justify-between items-md-center mb-8 gap-4 border-b pb-4">
                                <div>
                                    <h2 className="text-2xl font-bold text-gray-800">Bed Inventory</h2>
                                    <p className="text-sm text-gray-500">Live view of available medical facilities</p>
                                </div>

                                <div className="flex items-center space-x-3 bg-gray-50 p-2 rounded-lg border">
                                    <label htmlFor="specialty-filter" className="text-sm font-bold text-gray-600">Filter:</label>
                                    <select 
                                        id="specialty-filter"
                                        value={selectedSpecialty}
                                        onChange={handleFilterChange}
                                        className="bg-white border border-gray-300 rounded-md p-1.5 text-sm focus:ring-2 focus:ring-indigo-500 outline-none"
                                    >
                                        {SPECIALTY_OPTIONS.map(opt => <option key={opt} value={opt}>{opt}</option>)}
                                    </select>
                                </div>
                            </div>

                            {isLoading ? (
                                <div className="flex flex-col items-center justify-center py-20">
                                    <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-indigo-700 mb-4"></div>
                                    <p className="text-indigo-700 font-medium">Updating Bed Data...</p>
                                </div>
                            ) : (
                                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                                    {beds.length > 0 ? (
                                        beds.map(bed => (
                                            <BedCard 
                                                key={bed.bed_id} 
                                                bedData={bed} 
                                                onStatusChange={() => fetchBedData(selectedSpecialty)}
                                                token={token}
                                            />
                                        ))
                                    ) : (
                                        <div className="col-span-full text-center py-20 bg-gray-50 rounded-xl border-2 border-dashed border-gray-200">
                                            <p className="text-gray-400 text-lg italic">No beds currently registered for "{selectedSpecialty}"</p>
                                        </div>
                                    )}
                                </div>
                            )}
                        </section>
                    </div>

                    {/* SECTION 2: Patient Flow & Transfers (Right Side - 4/12 Columns) */}
                    <div className="xl:col-span-4 space-y-6">
                        <section className="sticky top-6 space-y-6">
                            <TransferRequestForm />
                            <TransferRequestList />
                        </section>
                    </div>
                </div>
            </main>
        </div>
    );
};

export default DashboardPage;